import React from 'react';
import { connect } from "react-redux"
import { NavBar, Icon, Toast, Button, Badge } from 'antd-mobile';
import { goodsDetail, addgoods, getusergoodsnum, dianzan, isdianzan1 } from "../actions/actions";
import { Link } from "react-router-dom"
import "./detail.less"
class DetailUI extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            page: 2
        }
    }
    componentDidMount() {
        this.props.init()

    }
    showToast = (loginInfo) => {
        Toast.info(loginInfo, 1);
    }
    goback = () => {
        this.props.history.goBack()
    }
    goLogin = () => {
        this.props.history.push("/user")
    }
    isdianzanCall = () => {
        var goodsID = this.props.match.params.goodsID
        var username = localStorage.username
        this.props.isdianzan2(goodsID, username)
    }
    render() {
        let goodsDetail = this.props.goodsDetail[0]
        let dom = null
        if (goodsDetail) {
            let { goodsprice, goodsimg, goodstitle } = goodsDetail
            dom = (
                <div className="detail">
                    <div className="imgcontent">
                        <img src={goodsimg} alt="" />
                    </div>
                    <p className="goodsprice">
                        <b>￥</b>
                        <span>{goodsprice.toFixed(2)}</span>
                        <i
                            style={this.props.isdianzan ? { color: "#8ec31f" } : { color: "#aaa" }}
                            onClick={localStorage.username ? this.props.dianzan(this.props.isdianzan, this.showToast, this.isdianzanCall) : this.goLogin}
                            className="iconfont icon-dianzan dianzan"
                        >
                        </i>
                    </p>
                    <h4 className="goodstitle">{goodstitle}</h4>
                    <div className="addGoods">
                        <div>
                            <p>
                                <Link to="/home">
                                    <img src="http://60.205.217.55:7000/images/shouye.svg" alt="" />
                                    <span>首页</span>
                                </Link>
                            </p>
                            <p className="p2">
                                <Link to={localStorage.username ? "/shoppingCar" : "/user"}>
                                    <img src="http://60.205.217.55:7000/images/gouwuche.svg" alt="" />
                                    <span>购物车</span>
                                </Link>
                                <Badge text={this.props.usergoodsnum} hot className="hot" />
                            </p>
                        </div>
                        <div>
                            <Button onClick={localStorage.username ? this.props.addgoods(goodsDetail.goodsprice, goodsDetail.goodsimg, goodsDetail.goodstitle, this.showToast) : this.goLogin}>加入购物车</Button>
                        </div>
                    </div>
                </div>

            )
        }
        return (
            <div>
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goback}
                >商品详情</NavBar>
                {dom}
            </div>
        )
    }

}
let mapDispathToProps = (dispatch, props) => {
    return {
        init() {
            Toast.loading('加载中...', 1, () => {
            })
            let goodsID = props.match.params.goodsID
            dispatch(goodsDetail(goodsID))
            dispatch(getusergoodsnum(localStorage.username))
            dispatch(isdianzan1(goodsID, localStorage.username))
        },
        addgoods: (goodsprice, goodsimg, goodstitle, cb) => () => {
            let goodsID = props.match.params.goodsID
            let username = localStorage.username
            dispatch(addgoods(goodsID, username, goodsprice, goodsimg, goodstitle, cb))
            // dispatch(getusergoodsnum(username))
        },
        dianzan: (isdianzan, cb1, cb2) => () => {
            let goodsID = props.match.params.goodsID
            let username = localStorage.username
            if (!isdianzan) {
                dispatch(dianzan(goodsID, username, cb1, cb2))
            }
        },
        isdianzan2: (goodsID, username) => {
            dispatch(isdianzan1(goodsID, username))
        }
    }
}
let mapStateToProps = state => {
    return {
        goodsDetail: state.detail.goodsDetail,
        usergoodsnum: state.detail.usergoodsnum,
        isdianzan: state.detail.isdianzan
    }
}
export let Detail = connect(mapStateToProps, mapDispathToProps)(DetailUI)