
import React from 'react';
import { NavBar, Icon, List, InputItem, WhiteSpace, Toast } from 'antd-mobile';
import { Address } from "../components/address"
import "./addgetgoods.less"
import { connect } from "react-redux"
import { addAress } from "../actions/actions";
export class AddgetgoodsUI extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,
        }
    }
    setShow = () => {

        this.setState({
            show: true
        })
    }
    delUsername = () => {
    }
    goback = () => {
        this.props.history.goBack()
    }
    showToast = (loginInfo) => {
        Toast.info(loginInfo, 1);
        this.props.history.push("/banlance")
    }

    //得到地址
    getAddress = (value) => () => {
        value = value.join("")
        this.setState({
            address: value
        })
        this.setState({
            show: false
        })
    }
    //得到姓名
    onChangename = (value) => {
        this.setState({
            name: value
        })
    }
    //得到电话
    onChangetel = (value) => {
        this.setState({
            tel: value
        })
    }
    //得到详细地址
    onChangedetAddress = (value) => {
        this.setState({
            detAddress: value
        })
    }
    render() {
        return (
            <div className="addAddresscontent">
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goback}
                >添加收货地址</NavBar>
                <List>
                    <WhiteSpace />
                    <InputItem
                        onBlur={this.onChangename}
                        type="text"
                    >收货人姓名:</InputItem>
                    <WhiteSpace />
                    <InputItem
                        type="text"
                        placeholder=""
                        onBlur={this.onChangetel}
                    >收货人手机:</InputItem>
                    <WhiteSpace />
                    <InputItem
                        type="text"
                        placeholder=""
                        onFocus={this.setShow}
                        value={this.state.address}
                    >收件人地址:</InputItem>
                    <WhiteSpace />
                    <InputItem
                        type="text"
                        placeholder=""
                        onBlur={this.onChangedetAddress}
                    >详细地址:</InputItem>
                    <WhiteSpace />
                </List>
                <div className="add_Address" onClick={this.props.addAress(this.state.name, this.state.address, this.state.tel, this.state.detAddress, this.showToast)}>添加收货地址</div>
                <Address show={this.state.show} getAddress={this.getAddress}></Address>
            </div>
        )
    }
}
let mapDispathToProps = (dispatch) => {
    return {
        addAress: (name, address, tel, detAddress, cb) => () => {
            var username = localStorage.username
            dispatch(addAress(name, address, tel, detAddress, username, cb))
        }
    }
}
let mapStateToProps = state => {
    return {

    }
}
export let Addgetgoods = connect(mapStateToProps, mapDispathToProps)(AddgetgoodsUI)