
import React from 'react';
import "./goodslist.less"
import { connect } from "react-redux"
import { getGoods } from '../../actions/actions';
import { Link } from "react-router-dom"
class ListUI extends React.Component {
    state = {
        list: [],
    }
    componentDidMount() {
        this.props.init()
    }
    render() {
        let domList = this.props.homeGoods.map(
            ({ _id, goodsprice, goodsimg, goodstitle }) => (
                <li className="moveIn" key={_id}>
                    <Link to={`/detail/${_id}`}>
                        <img src={goodsimg} alt="" />
                        <h4>{goodstitle}</h4>
                        <b>￥{goodsprice.toFixed(2)}</b>
                        <span className="hotgoodslei">热销产品</span>
                    </Link>
                </li>
            )
        )

        return (
            <div>
                <h3 className="hotGoods">热销产品</h3>
                <ul className="goodsList cl">
                    {domList}
                </ul>
            </div>
        )
    }
}

let mapDispathToProps = dispatch => {
    return {
        init() {
            dispatch(getGoods())
        }
    }
}
let mapStateToProps = state => {
    return {
        homeGoods: state.home.homeGoods
    }
}

export let Homelist = connect(mapStateToProps, mapDispathToProps)(ListUI)