
import React from 'react';
import { NavBar, Icon, List, InputItem, WhiteSpace, Toast } from 'antd-mobile';
import { Address } from "../components/address"
import "./addgetgoods.less"
import { connect } from "react-redux"
import { updAddress, getupdAddress } from "../actions/actions";
export class AddgetgoodsUI extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            show: false,
        }
    }

    componentWillMount() {
        this.props.init(this.props.match.params.addressId)
    }

    setShow = () => {

        this.setState({
            show: true
        })
    }
    delUsername = () => {
    }
    goback = () => {
        this.props.history.goBack()
    }
    showToast = (loginInfo) => {
        Toast.info(loginInfo, 1);
        this.props.history.push("/banlance")
    }
    //得到地址
    getAddress = (value) => () => {
        value = value.join("")
        this.setState({
            address: value
        })
        this.setState({
            show: false
        })
    }
    //得到姓名
    onChangename = (value) => {
        this.setState({
            name: value
        })
    }
    //得到电话
    onChangetel = (value) => {
        this.setState({
            tel: value
        })
    }
    //得到详细地址
    onChangedetAddress = (value) => {
        this.setState({
            detAddress: value
        })
    }
    render() {
        return (
            <div className="addAddresscontent">
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goback}
                >修改收货地址</NavBar>
                <List>
                    <WhiteSpace />
                    <InputItem
                        onChange={this.onChangename}
                        type="text"
                        defaultValue={this.props.updAddress1.name}
                    >收货人姓名:</InputItem>
                    <WhiteSpace />
                    <InputItem
                        type="text"
                        placeholder=""
                        onBlur={this.onChangetel}
                        defaultValue={this.props.updAddress1.tel}
                    >收货人手机:</InputItem>
                    <WhiteSpace />
                    <InputItem
                        type="text"
                        placeholder=""
                        onFocus={this.setShow}
                        value={this.state.address}
                    >收件人地址:</InputItem>
                    <WhiteSpace />
                    <InputItem
                        type="text"
                        placeholder=""
                        onBlur={this.onChangedetAddress}
                        defaultValue={this.props.updAddress1.detaddress}
                    >详细地址:</InputItem>
                    <WhiteSpace />
                </List>
                <div className="add_Address"
                    onClick={this.props.updAddress(
                        this.state.name ? this.state.name : this.props.updAddress1.name,
                        this.state.address ? this.state.address : this.props.updAddress1.address,
                        this.state.tel ? this.state.tel : this.props.updAddress1.tel,
                        this.state.detAddress ? this.state.detAddress : this.props.updAddress1.detaddress,
                        this.props.match.params.addressId,
                        this.showToast)}
                >确认修改</div>
                <Address show={this.state.show} getAddress={this.getAddress}></Address>
            </div>
        )
    }
}
let mapDispathToProps = (dispatch, props) => {
    return {
        updAddress: (name, address, tel, detAddress, _id, cb) => () => {
            let username = localStorage.username
            dispatch(updAddress(name, address, tel, detAddress, _id, username, cb))
        },
        init: (_id) => {
            dispatch(getupdAddress(_id))
        }
    }
}
let mapStateToProps = state => {
    return {
        updAddress1: state.user.updAddress
    }
}
export let Updpage = connect(mapStateToProps, mapDispathToProps)(AddgetgoodsUI)