import { REGISTER, USERADDRESS, DELADDRESS, GETUPDADDRESS } from "../actions/actionTypes"

export let user = (state = {
    initialPage: 1,
    address: [],
    updAddress: {}
}, action) => {
    let { type } = action
    let newState = { ...state }

    switch (type) {
        case REGISTER:
            newState.initialPage = action.initialPage
            return { ...state, ...{ initialPage: action.initialPage } }
        case USERADDRESS:
            return { ...state, ...{ address: action.address } }
        case DELADDRESS:
            var address = []
            state.address.forEach((item, index) => {
                if (item._id !== action._id) {
                    address.push(item)
                }
            })
            return { ...state, ...{ address } }
        case GETUPDADDRESS:
            var updAddress = {}
            state.address.forEach((item, index) => {
                if (item._id === action._id) {
                    updAddress = item
                }
            })
            return { ...state, ...{ updAddress } }
        default:
            return state

    }
}