import React from 'react';
import { HomeNav } from "../components/Nav/HomeNav"
import { Searchgoodslist } from '../components/GoodsList/Searchgoods';
import { Foot } from "../components/Foot/Foot"
export class Searchgoods extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            page: 1
        }
    }

    render() {
        return (
            <div>
                <HomeNav {...this.props}></HomeNav>
                <Searchgoodslist {...this.props}></Searchgoodslist>
                <Foot page={this.state.page} {...this.props}></Foot>
            </div>
        )
    }

}