import { REGISTER, HOMEGOODS, LISTGOODS, GOODSDETAIL, USERGOODSNUM, USERGOODS, USERADDRESS, DELADDRESS, GETUPDADDRESS, ISDIANZAN } from "./actionTypes";
import axios from "axios";

export let register = (username, password, cb) => dispatch => {
    axios.get("http://60.205.217.55:5800/react/register", { params: { username, password } }).then((res) => {

        if (res.data.code === 0) {
            cb(res.data.msg)
        } else {
            cb(res.data.msg)
            dispatch({
                type: REGISTER,
                initialPage: 0,
            })
        }
    })
}
export let login = (username, password, cb1, cb2) => dispatch => {
    axios.get("http://60.205.217.55:5800/react/login", { params: { username, password } }).then((res) => {
        if (res.data.code === 0) {
            cb2(res.data.msg)
        } else {
            cb1(res.data.msg)
            localStorage.username = username
        }
    })
}

