export let addgoods = (goodsID, username, goodsprice, goodsimg, goodstitle, cb) => dispatch => {
    axios.get("http://60.205.217.55:5800/react/addgoods", {
        params: { goodsID, username, goodsprice, goodsimg, goodstitle }
    }).then((res) => {
        cb(res.data.msg)
        if (res.data.type === 1) {
            axios.get("http://60.205.217.55:5800/react/getusergoodsnum", {
                params: { username }
            }).then((res) => {
                dispatch({
                    type: USERGOODSNUM,
                    usergoodsnum: res.data
                })
            })
        }
    })
}
