export let reduceGoods = (_id, username) => dispatch => {
    axios.get("http://60.205.217.55:5800/react/reduceGoods", {
        params: { _id }
    }).then((res) => {
        if (res.data.code) {
            axios.get("http://60.205.217.55:5800/react/getusergoods", {
                params: { username }
            }).then((res) => {
                dispatch({
                    type: USERGOODS,
                    usergoods: res.data
                })
                axios.get("http://60.205.217.55:5800/react/getusergoodsnum", {
                    params: { username }
                }).then((res) => {
                    dispatch({
                        type: USERGOODSNUM,
                        usergoodsnum: res.data
                    })
                })
            })
        }

    })
}
