//根据id 删除用户地址

export let delAddress = (_id, cb) => dispatch => {
    console.log(6666)
    axios.get("http://60.205.217.55:5800/react/delAddress", {
        params: { _id }
    }).then((res) => {
        if (res.data.code === 1) {
            dispatch({
                type: DELADDRESS,
                _id
            })
            cb(res.data.msg)
        }
    })
}
//根据id 获取要修改的地址

