import React from 'react';
import { NavBar, Icon, Tabs, InputItem, Button, Toast } from 'antd-mobile';
import "./user.less"
import { connect } from "react-redux"
import { register } from "../actions/actions";
import { login } from "../actions/actions";
class UserUI extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            username: null,
            hasErrorUsername: false,
            password: null,
            hasErrorUserpwd: false,
            username1: null,
            password1: null,
        }
    }
    goback = () => {
        this.props.history.goBack()

    }
    onChangeUsername = (value) => {
        if (!(/^[1][3,4,5,7,8][0-9]{9}$/.test(value))) {
            this.setState({
                hasErrorUsername: true,
            });
        } else {
            this.setState({
                hasErrorUsername: false,
            });
        }
        this.setState({
            username: value,
        });
    }
    onChangeUsername1 = (value) => {
        this.setState({
            username1: value,
        });
    }
    onChangeUserpwd = (value) => {
        if (!(/^[0-9a-zA-Z]{6,16}$/.test(value))) {
            this.setState({
                hasErrorUserpwd: true,
            });
        } else {
            this.setState({
                hasErrorUserpwd: false,
            });
        }
        this.setState({
            password: value,
        });
    }
    onChangeUserpwd1 = (value) => {
        this.setState({
            password1: value,
        });
    }
    showToast = (loginInfo) => {
        Toast.info(loginInfo, 1);
        this.props.history.push("/home")
    }
    showToast1 = (loginInfo) => {
        Toast.info(loginInfo, 1);
    }

    render() {
        const tabs = [
            { title: '快速注册' },
            { title: '密码登录' },
        ];
        return (
            <div>
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goback}
                >用户中心</NavBar>
                <h1>
                    <img src="https://pic.keede.com//app/images/login_topimg@2x.png" alt="" />
                </h1>
                <Tabs
                    tabs={tabs}
                    initialPage={this.props.initialPage}
                    animated={false}
                    useOnPan={false}
                    tabBarUnderlineStyle={{ border: "2px solid #000" }}
                    tabBarActiveTextColor={{ color: "#000" }}
                    tabBarInactiveTextColor={{ color: "#e8e8e8" }}
                >
                    <div style={{ backgroundColor: "#fff" }}>
                        <div className="username">
                            <InputItem
                                type="text"
                                placeholder="请输入常见手机号"
                                error={this.state.hasErrorUsername}
                                onErrorClick={this.onErrorClick}
                                onChange={this.onChangeUsername}
                                value={this.state.username}
                            >手机号码</InputItem>
                        </div>
                        <div className="userpwd">
                            <InputItem
                                type="password"
                                placeholder="请输入6-12位字母或数字密码"
                                error={this.state.hasErrorUserpwd}
                                onChange={this.onChangeUserpwd}
                                value={this.state.password}
                            >密码</InputItem>
                        </div>
                        <Button onClick={this.props.register(this.state.username, this.state.password, this.showToast1)}>立即注册</Button>
                    </div>
                    <div style={{}}>
                        <div className="username">
                            <InputItem
                                type="text"
                                placeholder="请输入手机号"
                                onChange={this.onChangeUsername1}
                                value={this.state.username1}
                            >手机号码</InputItem>
                        </div>
                        <div className="userpwd">
                            <InputItem
                                type="password"
                                placeholder="密码"
                                onChange={this.onChangeUserpwd1}
                                value={this.state.password1}
                            >密码</InputItem>
                        </div>
                        <Button onClick={this.props.login(this.state.username1, this.state.password1, this.showToast, this.showToast1)}>马上登录</Button>
                    </div>
                </Tabs>

            </div>
        )
    }

}
//return 返回的对象都指向UI组件的props

let mapDispathToProps = dispatch => {
    return {
        register: (username, password, cb) => () => {
            if (/^[1][3,4,5,7,8][0-9]{9}$/.test(username) && /^[0-9a-zA-Z]{6,16}$/.test(password)) {
                dispatch(register(username, password, cb))
            } else {
                cb("用户名密码格式不正确")
            }

        },
        login: (username, password, cb1, cb2) => () => {
            if (username && password) {
                dispatch(login(username, password, cb1, cb2))
            } else {
                cb2("请输入用户名和密码")
            }

        }
    }
}

//state 指向store里面的state
let mapStateToProps = state => {
    return {
        initialPage: state.user.initialPage
    }
}

export let User = connect(mapStateToProps, mapDispathToProps)(UserUI)