
import { GOODSDETAIL, USERGOODSNUM, USERGOODS, ISDIANZAN } from "../actions/actionTypes"
export let detail = (state = {
    goodsDetail: [],
    usergoodsnum: 0,
    usergoods: [],
    usergoodstotal: 0
}, action) => {
    let { type } = action
    let newState = { ...state }

    switch (type) {
        case GOODSDETAIL:
            newState.goodsDetail = action.goodsDetail
            return newState
        case USERGOODSNUM:
            return { ...state, ...{ usergoodsnum: action.usergoodsnum.count, usergoodstotal: action.usergoodsnum.total } }

        case USERGOODS:
            return { ...state, ...{ usergoods: action.usergoods } }
        case ISDIANZAN:
            return { ...state, ...{ isdianzan: action.isdianzan } }
        default:
            return state
    }
}