import { Route, Switch, Redirect } from "react-router-dom"
import { Home } from "./Home";
import { ShoppingCar } from "./ShoppingCar"
import React from 'react';
import { GoodsList } from "./GoodsList";
import { User } from "./User";
import { Detail } from "./Detail";
import { My } from "./My"
import { Searchgoods } from "./Searchgoods";
import { Banlance } from "./Banlance";
import { Addgetgoods } from "./Addgetgoods";
import { Updpage } from "./updpage.js"
import { BanlanceGoods } from "./BanlabceGoods"
export class Index extends React.Component {
    render() {
        return (
            <div>
                <Switch>
                    <Route path="/home" component={Home} />
                    <Route path="/shoppingCar" component={ShoppingCar} />
                    <Route path="/goodslist/:type" component={GoodsList} />
                    <Route path="/user" component={User} />
                    <Route path="/detail/:goodsID" component={Detail} />
                    <Route path="/my" component={My} />
                    <Route path="/searchgoods/:key" component={Searchgoods} />
                    <Route path="/banlancegoods" component={BanlanceGoods} />
                    <Route path="/banlance" component={Banlance}></Route>
                    <Route path="/addAddress" component={Addgetgoods}></Route>
                    <Route path="/updpage/:addressId" component={Updpage}></Route>
                    <Redirect exact={true} to="/home"></Redirect>

                </Switch>
            </div>
        )
    }
}