import React from 'react';
import { Foot } from "../components/Foot/Foot"
import { NavBar, Icon } from 'antd-mobile';
import { Usergoods } from '../components/usergoods';
export class ShoppingCar extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            page: 2,
            show: false
        }
    }
    goback = () => {
        this.props.history.goBack()
    }
    setShow = () => {
        if (this.state.show) {
            this.setState({
                show: false
            })
        } else {
            this.setState({
                show: true
            })
        }
    }
    render() {
        return (
            <div>
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goback}
                    rightContent={[
                        <span key="0" style={{ marginRight: '10px' }} onClick={this.setShow}>{this.state.show ? "完成" : "编辑"}</span>
                    ]}
                >购物车</NavBar>
                <Usergoods show={this.state.show} {...this.props}></Usergoods>
                <Foot page={this.state.page} {...this.props}></Foot>
            </div>
        )
    }

}