import { NavBar, Icon, Tabs } from 'antd-mobile';
import React from 'react';
import "./listnav.less"
import { connect } from "react-redux"
import { paixudesc } from '../../actions/actions'
class ListNavUI extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            type: null,
            title: null,
            Type: [
                { type: 1, title: "强生" },
                { type: 2, title: "日韩彩片" },
                { type: 3, title: "博士伦" },
                { type: 4, title: "海淘隐形" },
                { type: 5, title: "舒适隐形" },
                { type: 6, title: "万款花色" },
                { type: 7, title: "护理液" },
                { type: 8, title: "框架" },

            ]
        }
    }
    componentDidMount() {
        this.state.Type.forEach((item) => {
            if (item.type === this.props.match.params.type * 1) {
                this.setState({
                    title: item.title,
                    desc: 0
                })
            }
        })
    }
    goback = () => {
        this.props.history.goBack()
    }
    priceDesc = () => {

    }

    render() {
        const tabs2 = [
            { title: '综合', sub: '1' },
            { title: '销量', sub: '2' },
            { title: '价格', sub: '3' },
            { title: '筛选', sub: '4' },
        ];

        return (
            <div className="listGoods">
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goback}
                    rightContent={[
                        <Icon key="0" type="search" style={{ marginRight: '10px' }} />
                    ]}
                >{this.state.title}</NavBar>
                <ul className="paixu">
                    <Tabs tabs={tabs2}
                        initialPage={2}
                        tabBarPosition="bottom"
                        tabBarUnderlineStyle={{ border: "1px solid #8ec31f" }}
                        renderTab={tab => tab.sub * 1 === 2 || 3 ? <span onClick={this.props.desc(this.props.match.params.type, this.state.desc, tab.sub)}>{tab.title}</span> : <span>{tab.title}</span>}
                    >
                    </Tabs>
                </ul>
            </div>
        )
    }
}
let mapDispathToProps = dispatch => {
    return {
        desc: (goodstype, desc, desckey) => () => {
            desc += 1
            var paixu = null
            if (desc % 2 === 0) {
                paixu = -1

            } else {
                paixu = 1

            }
            dispatch(paixudesc(goodstype, paixu, desckey))
        }
    }
}
let mapStateToProps = state => {
    return {
    }

}

export let ListNav = connect(mapStateToProps, mapDispathToProps)(ListNavUI)