import React from 'react';
import { ListNav } from "../components/Nav/ListNav"
import { List } from "../components/GoodsList/List"
export class GoodsList extends React.Component {
    constructor(props) {
        super(props)
        this.state = {}
    }

    render() {
        return (
            <div>
                <ListNav {...this.props}></ListNav>
                <List {...this.props}></List>
            </div>
        )
    }

}