
import React from 'react';
import "./index.less";
import { connect } from "react-redux"
import { getusergoods, reduceGoods, carAddgoods, deleteGoods, salesGoods } from '../../actions/actions';
import { Toast, Icon, Modal } from 'antd-mobile';


export class UsergoodsUI extends React.Component {
    componentDidMount() {
        this.props.init(localStorage.username)
    }
    showToast = () => {
        Toast.info("亲,商品已经再能再减少了", 2);
    }
    onChange = () => {
        console.log("")
    }
    banlancegoods = () => {
        this.props.history.push("/banlancegoods")
    }
    render() {
        let dom = null
        if (this.props.usergoods[0]) {
            const usergoodslist = this.props.usergoods.map((item, index) => (
                <div key={index} className="usergoodscontent">
                    <div className="imgcontent">
                        <img src={item.goodsimg} alt="" />
                    </div>
                    <div className="goodsdetail">
                        <p className="cl">
                            <span>{item.goodstitle}</span>
                            <strong>￥{item.goodsprice}</strong>
                        </p>
                        <div className="changeGoods" >
                            <span onClick={item.goodsnum > 1 ? this.props.reduceGoods(item._id, localStorage.username) : this.showToast}>-</span>
                            <p >
                                <input type="text" value={item.goodsnum} onChange={this.onChange} />
                            </p>
                            <span onClick={this.props.addGoods(item._id, localStorage.username)}>+</span>
                        </div>
                    </div>
                    <span className="delete" style={this.props.show ? { display: "block" } : { display: "none" }} onClick={() => {
                        Modal.alert('确认操作', '是否确认将该商品从购物车移除', [
                            { text: '取消' },
                            { text: '确定', onPress: () => { this.props.deleteGoods(item._id, localStorage.username) } },
                        ])
                    }}>
                        <Icon type="cross-circle" />
                    </span>
                </div>


            ))
            dom = (<div>
                <div className="cartitle">可得自营</div>
                {usergoodslist}
                <div className="carTotal cl">
                    <p className="p1">合计:￥{this.props.usergoodstotal}元(不含运费)</p>
                    <p onClick={this.props.salesGoods(this.props.usergoods, this.banlancegoods)} className="p2">结算{"(" + this.props.usergoodsnum + ")"}</p>
                </div>
            </div>)
        } else {
            dom = <h3 className="noGoods">你的购物车还没有商品，快去选购</h3>
        }



        return (<div className="usergoodscar">
            {dom}
        </div>);
    }
}

let mapDispathToProps = dispatch => {
    return {
        init(username) {
            dispatch(getusergoods(username))
        },
        reduceGoods: (_id, username) => () => {
            dispatch(reduceGoods(_id, username))
        },
        addGoods: (_id, username) => () => {
            dispatch(carAddgoods(_id, username))
        },
        deleteGoods(_id, username) {
            dispatch(deleteGoods(_id, username))
        },
        salesGoods: (usergoods, cb) => () => {
            cb()
            usergoods.forEach((item) => {
                dispatch(salesGoods(item.goodsID, item.goodsnum))
            })

        }
    }
}
let mapStateToProps = state => {
    return {
        usergoods: state.detail.usergoods,
        usergoodsnum: state.detail.usergoodsnum,
        usergoodstotal: state.detail.usergoodstotal
    }

}

export let Usergoods = connect(mapStateToProps, mapDispathToProps)(UsergoodsUI)