import { TabBar, Toast } from 'antd-mobile';
import React from 'react';
import "./foot.less"
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import { getusergoodsnum } from "../../actions/actions";


class FootUI extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: this.props.page,
            hidden: false,
            fullScreen: false,
        };
    }
    showToast = (loginInfo) => {
        Toast.info(loginInfo, 1);
    }
    componentWillMount() {
        this.props.init()
    }

    render() {
        return (
            <TabBar
                unselectedTintColor="#949494"
                tintColor="#8ec31f"
                barTintColor="white"
                hidden={this.state.hidden}

            >
                {/* 首页 */}
                <TabBar.Item
                    title="首页"
                    key="Life"
                    icon={<div style={{
                        width: '22px',
                        height: '22px',
                        background: 'url(http://60.205.217.55:7000/images/shouye.svg) center center /  21px 21px no-repeat'
                    }}
                    />
                    }
                    selectedIcon={<div style={{
                        width: '22px',
                        height: '22px',
                        background: 'url(http://60.205.217.55:7000/images/selshouye.svg) center center /  21px 21px no-repeat',
                    }}
                    />
                    }
                    selected={this.state.selectedTab === 0}
                    onPress={() => {
                        this.props.history.push("/home")
                    }}
                    data-seed="logId"
                >
                    <Link to="/home"></Link>
                </TabBar.Item>
                {/* 毒物 */}
                <TabBar.Item
                    icon={
                        <div style={{
                            width: '22px',
                            height: '22px',
                            background: 'url(http://60.205.217.55:7000/images/duwu.svg) center center /  21px 21px no-repeat'
                        }}
                        />
                    }
                    selectedIcon={
                        <div style={{
                            width: '22px',
                            height: '22px',
                            background: 'url(http://60.205.217.55:7000/images/selduwu.svg) center center /  21px 21px no-repeat'
                        }}
                        />
                    }
                    title="毒物"
                    key="Koubei"
                    selected={this.state.selectedTab === 1}
                    onPress={() => {
                        this.props.history.push("/searchgoods/''")
                    }}
                    data-seed="logId1"
                >

                </TabBar.Item>
                {/* 购物车 */}
                <TabBar.Item
                    icon={
                        <div style={{
                            width: '22px',
                            height: '22px',
                            background: 'url(http://60.205.217.55:7000/images/gouwuche.svg) center center /  21px 21px no-repeat'
                        }}
                        />
                    }
                    selectedIcon={
                        <div style={{
                            width: '22px',
                            height: '22px',
                            background: 'url(http://60.205.217.55:7000/images/selgouwuche.svg) center center /  21px 21px no-repeat'
                        }}
                        />
                    }
                    title="购物车"
                    badge={this.props.usergoodsnum}
                    key="Friend"
                    selected={this.state.selectedTab === 2}
                    onPress={() => {
                        if (localStorage.username) {
                            this.props.history.push("/shoppingCar")
                        } else {
                            this.props.history.push("/user")
                        }
                    }}
                >
                </TabBar.Item>
                {/* 个人中心 */}
                <TabBar.Item
                    icon={{ uri: 'http://60.205.217.55:7000/images/my.svg' }}
                    selectedIcon={{ uri: 'http://60.205.217.55:7000/images/selmy.svg' }}
                    title="我的"
                    key="my"
                    selected={this.state.selectedTab === 3}
                    onPress={() => {
                        if (localStorage.username) {
                            this.props.history.push("/my")
                        } else {
                            this.props.history.push("/user")
                        }
                    }}
                >
                </TabBar.Item>
            </TabBar>
        );
    }
}


let mapDispathToProps = (dispatch) => {
    return {
        init() {
            Toast.loading('加载中...', 1, () => {
                dispatch(getusergoodsnum(localStorage.username))
            })
        },
    }
}
let mapStateToProps = state => {
    return {
        usergoodsnum: state.detail.usergoodsnum,
    }
}

export let Foot = connect(mapStateToProps, mapDispathToProps)(FootUI)

