
import React from 'react';
import { NavBar, Icon, SwipeAction, List, Toast, Button } from 'antd-mobile';
import { } from "../components/address"
import "./my.less"
import { connect } from "react-redux"
import { getAddress, delAddress, getupdAddress } from "../actions/actions";
export class BanlanceUI extends React.Component {
    constructor(props) {
        super(props)
        this.state = {

        }
    }
    componentDidMount() {
        this.props.init()
    }
    delUsername = () => {
    }
    goback = () => {
        this.props.history.goBack()
    }
    showToast = (loginInfo) => {
        Toast.info(loginInfo, 1);
    }
    goaddAdress = () => {
        this.props.history.push("/addAddress")
    }
    goupdpage = (_id) => {
        this.props.history.push(`/updpage/${_id}`)
        this.props.getupdAddress(_id)
    }
    render() {
        let dom = []
        if (this.props.address[0]) {
            this.props.address.forEach((item, index) => {
                let addressobj =
                    <div key={index}>
                        <SwipeAction

                            style={{ backgroundColor: "#fff", paddingTop: '1rem', touchAction: "none" }}
                            autoClose
                            right={[
                                {
                                    text: '修改',
                                    onPress: () => { this.goupdpage(item._id) },
                                    style: { backgroundColor: '#ddd', color: 'white' },
                                },
                                {
                                    text: '删除',
                                    onPress: () => { this.props.delAddress(item._id, this.showToast) },
                                    style: { backgroundColor: '#F4333C', color: 'white' },
                                },
                            ]}

                        >
                            <List.Item
                                arrow="horizontal"
                            >
                                <h4><span style={{ color: "#aaa" }}>地址:</span><span style={{ color: "#a8a8a8" }}>{index + 1}</span></h4>
                                <p><span style={{ color: "#aaa" }}>收件人姓名:</span><span style={{ color: "#a8a8a8" }}>{item.name}</span></p>
                                <p><span style={{ color: "#aaa" }}>收件人电话:</span><span style={{ color: "#a8a8a8" }}>{item.tel}</span></p>
                                <p><span style={{ color: "#aaa" }}>收件人地址:</span><span style={{ color: "#a8a8a8" }}>{item.address}</span></p>
                                <p><span style={{ color: "#aaa" }}>收件人详细地址:</span><span style={{ color: "#a8a8a8" }}>{item.detaddress}</span></p>
                            </List.Item>
                        </SwipeAction>
                    </div>
                dom.push(addressobj)
            })

        } else {
            dom = <div>

                <h4 style={{ marginTop: "1rem", lineHeight: "0.6rem", textAlign: "center", color: "#777" }}>暂时没有收货地址快去添加</h4>

            </div>

        }
        return (
            <div style={{ backgroundColor: "#fff" }}>
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goback}
                >请选择收货地址</NavBar>
                {dom}
                <Button onClick={this.goaddAdress}>添加收货地址</Button>
            </div>
        )
    }
}
let mapDispathToProps = (dispatch) => {
    return {
        init: () => {
            let username = localStorage.username
            dispatch(getAddress(username))
        },
        delAddress: (_id, cb) => {
            dispatch(delAddress(_id, cb))
        },
        getupdAddress: (_id) => {
            dispatch(getupdAddress(_id))
        }

    }
}
let mapStateToProps = state => {
    return {
        address: state.user.address
    }
}

export let Banlance = connect(mapStateToProps, mapDispathToProps)(BanlanceUI)

