
import { HOMEGOODS } from "../actions/actionTypes"
export let home = (state = {

    homeGoods: []
}, action) => {
    let { type } = action
    let newState = { ...state }

    switch (type) {
        case HOMEGOODS:
            newState.homeGoods = action.homeGoods
            return newState
        default:
            return state
    }
}