import { LISTGOODS } from "../actions/actionTypes";
export let list = (state = {
    listGoods: []
}, action) => {
    let { type } = action
    switch (type) {
        case LISTGOODS:
            return { ...state, ...{ listGoods: action.listGoods } }
        case "suijiGoods":
            return { ...state, ...{ listGoods: state.listGoods.reverse() } }
        default:
            return state

    }
}