import { SearchBar } from 'antd-mobile';
import React from 'react';
import "./homenav.less";
import { connect } from "react-redux"
import { getsearchGoods } from '../../actions/actions'
class HomeNavUI extends React.Component {
    state = {

    };
    onChange = (value) => {
        this.setState({ value });
    };

    render() {
        return (<div>
            <SearchBar
                onSubmit={this.props.setKeyValue(this.state.value, this.props)}
                onChange={this.onChange}
                value={this.state.value}
                placeholder="请输入你想要购买的商品"
                maxLength={10}
                type="search" />
        </div>);
    }
}
let mapDispathToProps = (dispatch) => {
    return {
        setKeyValue: (key, props) => () => {
            dispatch(getsearchGoods(key))
            props.history.push(`/searchgoods/${key}`)

        }
    }
}
let mapStateToProps = state => {
    return {

    }

}

export let HomeNav = connect(mapStateToProps, mapDispathToProps)(HomeNavUI)









