import { connect } from "react-redux"
import React from 'react';
import { HomeNav } from "../components/Nav/HomeNav"
import { Banner } from "../components/banner/banner"
import { Foot } from "../components/Foot/Foot"
import { GoodsType } from "../components/jiugongge/GoodsType";
import { Homelist } from "../components/GoodsList/Homelist";
class UI extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            page: 0
        }
    }

    render() {
        return (
            <div>
                <HomeNav {...this.props}></HomeNav>
                <Banner {...this.props}></Banner>
                <GoodsType></GoodsType>
                <Homelist></Homelist>
                <Foot page={this.state.page} {...this.props}></Foot>
            </div>
        )
    }

}

export let Home = connect()(UI)