import React from 'react';
import { PickerView } from 'antd-mobile';
import { connect } from "react-redux"
import axios from "axios";
import "./index.less"
class AddressUI extends React.Component {
    state = {
        value: null,
        province: [],
    };
    onChange = (value) => {
        this.setState({
            value,
        });
    }

    componentDidMount() {
        axios.get("http://api.yytianqi.com/citylist/id/2").then((res) => {
            var arr = []
            for (var i = 0; i < res.data.list.length; i++) {
                var obj = {}
                obj.value = res.data.list[i].name
                obj.label = res.data.list[i].name
                obj.children = []
                for (var j = 0; j < res.data.list[i].list.length; j++) {
                    var children = {}
                    children.value = res.data.list[i].list[j].name
                    children.label = res.data.list[i].list[j].name
                    children.children = []
                    if (res.data.list[i].list[j].list) {
                        for (var e = 0; e < res.data.list[i].list[j].list.length; e++) {
                            var children2 = {}
                            children2.value = res.data.list[i].list[j].list[e].name
                            children2.label = res.data.list[i].list[j].list[e].name
                            children.children.push(children2)
                        }
                    }
                    obj.children.push(children)
                }
                arr.push(obj)
            }
            this.setState({
                province: arr
            })
        })

    }
    render() {
        let dom = <PickerView
            onChange={this.onChange}
            data={this.state.province}
            value={this.state.value}
        />
        return (
            <div className="slcAddress" style={this.props.show ? { height: "5.5rem" } : { height: 0 }}>
                <div className="okAddress" onClick={this.props.getAddress(this.state.value)}>确定</div>
                {dom}
            </div>
        );
    }
}
let mapDispathToProps = (dispatch, props) => {
    return {

    }
}
let mapStateToProps = state => {
    return {

    }
}

export let Address = connect(mapStateToProps, mapDispathToProps)(AddressUI)