import React from 'react';
import { connect } from "react-redux"
import { NavBar, Icon, Button } from 'antd-mobile';
import { getusergoods } from '../actions/actions';

class BanlanceGoodsUI extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
        }
    }
    goback = () => {
        this.props.history.goBack()
    }
    componentDidMount() {
        this.props.init(localStorage.username)
    }
    goBanlance = () => {
        this.props.history.push("/banlance")
    }
    render() {
        let dom = []
        this.props.usergoods.forEach((item, index) => {

            let usergoodsobj =
                <div style={{ paddingTop: "0.9rem", paddingLeft: "0.4rem" }} className="cl" key={index}>
                    <div style={{ width: "1.84rem", float: "left" }}>
                        <img style={{ width: "100%" }} src={item.goodsimg} alt="" />
                    </div>
                    <p style={{ float: "left", width: "50%", lineHight: "0.4rem", margin: "0.5rem" }}>{item.goodstitle}</p>
                </div >
            dom.push(usergoodsobj)
        })
        return (
            <div>
                <NavBar
                    mode="light"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goback}
                >商品结算</NavBar>
                <Button onClick={this.goBanlance} style={{ marginTop: "1rem" }}>快去选择收货地址</Button>
                {dom}
            </div >
        )
    }

}




let mapDispathToProps = (dispatch) => {
    return {
        init(username) {
            dispatch(getusergoods(username))
        },
    }
}
let mapStateToProps = state => {
    return {
        usergoods: state.detail.usergoods,
    }
}
export let BanlanceGoods = connect(mapStateToProps, mapDispathToProps)(BanlanceGoodsUI)