
import React from 'react';
import { Foot } from "../components/Foot/Foot"
import { Button } from 'antd-mobile';
import "./my.less"
export class My extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            page: 3
        }
    }
    delUsername = () => {
        localStorage.username = ""
        this.props.history.push("/user")
    }
    render() {
        return (
            <div>
                <div className="my">
                    <div className="my-ponto">
                        <img src="https://pic.keede.com//app/images/login_img.png" alt="" />
                    </div>
                    <div className="my-name">
                        <p className="p1">{localStorage.username ? localStorage.username : "尚未登录"}</p>
                        <p className="p2">普通会员</p>
                    </div>
                </div>
                <Button onClick={this.delUsername} type="warning">退出登录</Button>
                <Foot page={this.state.page} {...this.props}></Foot>
            </div>
        )
    }
}