
import React from 'react';
import "./goodslist.less"
import { connect } from "react-redux"
import { suijiGoods, getsearchGoods } from '../../actions/actions'
import { Link } from "react-router-dom"
import { PullToRefresh, Toast } from 'antd-mobile';

class SearchgoodsUI extends React.Component {
    state = {
        refreshing: false
    }
    componentDidMount() {
        this.props.init(this.props.match.params.key)
    }

    render() {
        let dom = null
        if (this.props.listGoods[0]) {
            dom = <PullToRefresh
                damping={60}
                ref="goods"
                style={{
                    overflow: 'auto',
                }}
                indicator={{ deactivate: '上拉可以刷新' }}
                direction={'down'}
                refreshing={this.state.refreshing}
                onRefresh={() => {    // 下拉刷新 
                    this.setState({ refreshing: true });
                    setTimeout(() => {
                        this.props.suijiGoods()
                        this.setState({ refreshing: false });
                    }, 2000);
                }}
            >
                <ul className="goodsList cl">
                    {
                        this.props.listGoods.map(
                            ({ _id, goodsprice, goodsimg, goodstitle }) => (
                                <li className="moveIn" key={_id}>
                                    <Link to={`/detail/${_id}`}>
                                        <img src={goodsimg} alt="" />
                                        <h4>{goodstitle}</h4>
                                        <b>￥{goodsprice.toFixed(2)}</b>
                                    </Link>
                                </li>
                            )
                        )
                    }
                </ul>
            </PullToRefresh>
        } else {
            dom = <h3 className="noGoods">请输入你想要查找商品的关键字</h3>
        }
        return (

            <div>
                {dom}
            </div>
        )
    }
}

let mapDispathToProps = dispatch => {
    return {
        init(key) {
            Toast.loading('加载中...', 1, () => {
            })
            dispatch(getsearchGoods(key))

        },
        suijiGoods() {
            dispatch(suijiGoods())
        }
    }
}
let mapStateToProps = state => {
    return {
        listGoods: state.list.listGoods
    }

}

export let Searchgoodslist = connect(mapStateToProps, mapDispathToProps)(SearchgoodsUI)